.container {
    margin-top: 200px;
    position: absolute;
    width: 100%;
    min-height: 70vh;
    padding: 2rem;
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 1;
}
.form {
    width: 100%;
    max-width: 820px;
    background-color: rgb(0, 0, 0);
    border-radius: 10px;
    box-shadow: 0 0 20px 5px rgba(0, 0, 0, 0.8);
    z-index: 10;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    font-family: 'Rubik', sans-serif;
}
.contact-form {
    background-color: rgb(17, 21, 29);
    position: relative;
}
.contact-form:before {
    content: "";
    position: absolute;
    width: 26px;
    height: 26px;
    background-color: rgb(17, 21, 29);
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 50px;
    left: -13px;
}
form {
    padding: 2.3rem 2.2rem;
    z-index: 10;
    overflow: hidden;
    position: relative;
}
.title {
    color: #fff;
    font-size: 1.6rem;
    line-height: 1;
    margin-bottom: 0.7rem;
}
.input-container {
    position: relative;
    margin: 1rem 0;
}
.input {
    width: 100%;
    outline: none;
    border: 2px solid #fafafa;
    background: none;
    padding: 0.6rem 0.5rem;
    color: #fff;
    font-weight: 500;
    font-size: 0.95rem;
    letter-spacing: 0.5px;
    border-radius: 25px;
    transition: 0.3s;
}
textarea.input {
    padding: 0.8rem 0.5rem;
    min-height: 150px;
    border-radius: 22px;
    resize: none;
    background-color: rgb(17, 21, 29);
    overflow-y: auto;
}
.input-container label {
    position: absolute;
    top: 50%;
    left: 15px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    padding: 0 0.4rem;
    color: #fafafa;
    font-size: 0.9rem;
    font-weight: 400;
    pointer-events: none;
    z-index: 1000;
    transition: 0.5s;
}
.input-container.textarea label {
    top: 1rem;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.btn {
    padding: 0.6rem 1.3rem;
    background-color: rgb(237, 236, 236);
    border: 2px solid #fafafa;
    font-size: 1rem;
    color: #000;
    line-height: 1;
    border-radius: 25px;
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    margin: 0;
    font-family: 'Rubik', sans-serif;
}
.btn:hover {
    background-color: transparent;
    color: #fff;
}
.input-container span {
    position: absolute;
    top: 0;
    left: 25px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 0.8rem;
    padding: 0 0.4rem;
    color: transparent;
    pointer-events: none;
    z-index: 500;
}
.input-container span:before,
.input-container span:after {
    content: "";
    position: absolute;
    width: 10%;
    opacity: 0;
    transition: 0.3s;
    height: 5px;
    background-color: #000;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.input-container span:before {
    left: 50%;
}
.input-container span:after {
    right: 50%;
}
.input-container.focus label {
    top: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    left: 25px;
    font-size: 0.8rem;
}
.input-container.focus span:before,
.input-container.focus span:after {
    width: 50%;
    opacity: 1;
}
